import neilModal from '../../components/neil-modal/neil-modal.vue'
export default {
	data() {
		return {
			show: false,
			content: '',
			value: ''
		};
	},
	components: {
		neilModal
	},
	methods: {
		showModal() {
			this.show = true;
		},
		closeModal() {
			uni.showToast({
				title: '关闭了弹窗'
			});
			this.show = false;
		},
		updateContent() {
			this.content = this.value;
			uni.showToast({
				title: '更新了文本'
			});
			this.show = false;
		}
	}
}
